<template>
    <div class="">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item><i class="el-icon-cloudy"/> </el-breadcrumb-item>
                <el-breadcrumb-item><i class="el-icon-alarm-clock"/> </el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <div class="container">
            <el-button type="primary" @click="addClock"></el-button>

            <el-button type="success" @click="dialogFormVisible = true"></el-button>
            <el-dialog :title="getTitle" :visible.sync="dialogFormVisible" @close="resetObj">
                <el-form ref="form" :model="clockVO">

                    <el-form-item label="" label-width="100px">
                        <el-date-picker
                                v-model="clockVO.clockDay"
                                type="date"
                                align="left"
                                :default-value="getDate"
                                value-format="yyyy-MM-dd"
                                :picker-options="pickerOptions">
                        </el-date-picker>
                    </el-form-item>

                    <el-form-item label="" prop="region" label-width="100px">
                        <el-select v-model="clockVO.lunarMonth" filterable placeholder="">
                            <el-option v-for="item in lunarMonth"
                                       :key="item.value"
                                       :value="item.value"
                                       :label="item.value"
                                       :disabled="item.disabled"/>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="region" label-width="100px">
                        <el-select v-model="clockVO.lunarDay" filterable placeholder="">
                            <el-option v-for="item in lunarDay"
                                       :key="item.value"
                                       :value="item.value"
                                       :label="item.value"
                                       :disabled="item.disabled"/>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="region" label-width="100px">
                        <el-select v-model="clockVO.week" filterable placeholder="">
                            <el-option
                                    v-for="item in week"
                                    :key="item.value"
                                    :value="item.value"
                                    :label="item.label"
                                    :disabled="item.disabled"/>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="24" prop="region" label-width="100px">
                        <el-select v-model="clockVO.solarTerms" filterable clearable placeholder="">
                            <el-option
                                    v-for="item in solarTerm"
                                    :key="item.value"
                                    :label="item.value"
                                    :value="item.value"
                                    :disabled="item.disabled"/>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="region" label-width="100px">
                        <el-select v-model="clockVO.holiday" filterable clearable placeholder="">
                            <el-option
                                    v-for="item in holiday"
                                    :key="item.value"
                                    :label="item.value"
                                    :value="item.value"
                                    :disabled="item.disabled"/>
                        </el-select>
                    </el-form-item>

                </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="cancelOperation"> </el-button>
                    <el-button type="primary" @click="addOrUpdate()"> </el-button>
                </div>

            </el-dialog>

            <div style="margin: 20px;"></div>

            <el-table :data="records" style="width: 100%" :header-cell-style="{background: '#F6F7FB'}" border>
                <el-table-column align="center" fixed label="" prop="clockDay"/>
                <el-table-column align="center" label="" prop="weekname"/>
                <el-table-column align="center" label="" prop="lunarMonth"/>
                <el-table-column align="center" label="" prop="lunarDay"/>
                <el-table-column align="center" label="" prop="holiday"/>
                <el-table-column align="center" label="24" prop="solarTerms"/>
                <el-table-column align="center" label="" prop="createTime"/>
                <el-table-column align="center" fixed="right" label="">
                    <template slot-scope="scope">
                        <el-button @click="setObj(scope.row.id)" type="text" icon="el-icon-edit"></el-button>
                        <el-button @click="deleteById(scope.row.id)" type="text" icon="el-icon-delete" style="color: red"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <br>

            <!--  -->
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageCurrent"
                    :page-sizes="[15,30,50,100]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </div>
    </div>
</template>

<style scoped src="../../assets/css/application/application.css"/>

<script>
    import clock from "../../api/application/clock"
    import moment from "moment";

    export default {
        name: "",
        data() {
            return ({
                pageCurrent: 1,
                pageSize: 15,
                total: 0,
                pages: 0,
                records: [],
                clockVO: {
                    id: null,
                    clockDay: "", //
                    solarTerms: "", //24
                    holiday: "", //
                    week: null, //
                    lunarMonth: "", //
                    lunarDay: "", //
                },
                lunarMonth: [{
                    value: '',
                    disabled: true
                }, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}],
                lunarDay: [{
                    value: '',
                    disabled: true
                }, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}],
                week: [{
                    value: '',
                    disabled: true
                }, {value: '0', label: ''}, {value: '1', label: ''}, {value: '2', label: ''}, {
                    value: '3',
                    label: ''
                }, {value: '4', label: ''}, {value: '5', label: ''}, {value: '6', label: ''}],
                solarTerm: [{
                    value: '',
                    disabled: true
                }, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}],
                holiday: [{
                    value: '',
                    disabled: true
                }, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}],
                dialogFormVisible: false,
                pickerOptions: {
                    shortcuts: [{
                        text: '',
                        onClick(picker) {
                            picker.$emit('pick', new Date());
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24);
                            picker.$emit('pick', date);
                        }
                    }, {
                        text: '',
                        onClick(picker) {
                            const date = new Date();
                            date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                            picker.$emit('pick', date);
                        }
                    }]
                }
            })
        },
        created() {
            this.getClock(1)
        },
        methods: {
            getClock(pageCurrent) {
                this.pageCurrent = pageCurrent;
                clock.getClockList(pageCurrent, this.pageSize)
                    .then(response => {
                        let data = response.data;
                        this.records = data.records;
                        this.total = data.total;
                        this.pages = data.pages;
                        this.current = data.current;
                        this.size = data.size;
                    }).catch(error => {
                    console.error(error)
                })
            },
            addOrUpdate() {
                if (this.clockVO.id == null) {
                    this.addDiyClock()
                } else {
                    this.updateClock()
                }
            },
            setObj(id) {
                let list = this.records.filter(record => {
                    return record.id === id
                });
                let data = list.pop();
                this.clockVO.id = data.id;
                this.clockVO.clockDay = data.clockDay;
                this.clockVO.solarTerms = data.solarTerms;
                this.clockVO.holiday = data.holiday;
                this.clockVO.week = data.week + '';
                this.clockVO.lunarMonth = data.lunarMonth;
                this.clockVO.lunarDay = data.lunarDay;

                this.dialogFormVisible = true;
            },
            resetObj() {
                this.clockVO = {
                    id: null,
                    clockDay: "",
                    solarTerms: "",
                    holiday: "",
                    week: null,
                    lunarMonth: "",
                    lunarDay: "",
                };

            },
            cancelOperation() {
                this.resetObj();
                this.dialogFormVisible = false;
            },
            addClock() {
                clock.addClock()
                    .then(response => {
                        this.getClock(this.pageCurrent);
                        this.$message({
                            type: "success",
                            message: "!"
                        })
                    }).catch(error => {
                    console.log(error)
                })
            },
            addDiyClock() {
                clock.addDiyClock(this.clockVO)
                    .then(response => {
                        this.getClock(this.pageCurrent);
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                        this.dialogFormVisible = false;
                        this.resetObj();
                    }).catch(error => {
                    console.log(error)
                })
            },
            updateClock() {
                clock.updateClock(this.clockVO)
                    .then(response => {
                        this.getClock(this.pageCurrent);
                        this.$message({
                            type: 'success',
                            message: '!'
                        });
                        this.dialogFormVisible = false;
                        this.resetObj();
                    }).catch(error => {
                    console.log(error)
                })
            },
            deleteById(id) {
                this.$confirm('', '', {
                    confirmButtonClass: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    clock.deleteClock(id)
                        .then(response => {
                            this.getClock(this.pageCurrent);
                            this.$message({
                                type: "success",
                                message: "!"
                            })
                        }).catch(error => {
                        console.log(error)
                    })
                }).catch(error => {
                    console.log(error)
                })
            },
            //
            handleSizeChange(pageSize) {
                this.pageSize = pageSize;
                this.getClock(this.pageCurrent)
            },
            handleCurrentChange(pageCurrent) {
                this.pageCurrent = pageCurrent;
                this.getClock(this.pageCurrent)
            }
        },
        computed: {
            getTitle() {
                if (this.clockVO.id == null) {
                    return ""
                } else {
                    return ""
                }
            },
            getDate() {
                if (this.clockVO.clockDay === '') {
                    this.clockVO.clockDay = moment().format('YYYY-MM-DD');
                }
            }
        },
        watch: {
            records() {
                this.records.map(record => {
                    let week = record.week;
                    switch (week) {
                        case 0:
                            record.weekname = "";
                            break;
                        case 1:
                            record.weekname = "";
                            break;
                        case 2:
                            record.weekname = "";
                            break;
                        case 3:
                            record.weekname = "";
                            break;
                        case 4:
                            record.weekname = "";
                            break;
                        case 5:
                            record.weekname = "";
                            break;
                        case 6:
                            record.weekname = "";
                            break;
                        default:
                            record.weekname = "Error";
                            break;
                    }
                })
            }
        }
    }

</script>
